input {
  text-align: right;
}

.container {
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
}

.keypad {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

#clear {
  grid-column: 1/3;
}

#equal {
  grid-column: 3/5;
  grid-row: 5;
}
